html,
body {
    height: 100%;
    /* font-family: 'IBM Plex Sans'; */
    background: #fff;
}

.border {
    border: 1px solid #000
}

.containerwrap {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    height: 100%;
}

.iblock {
    display: inline-block;
}

.redcolor {
    color: red;
}

.headerbox {
    background: #F4F7FB;
}

.sidebar {
    background: white;
    width: 210px;
    min-width: 210px;
    /* border-right: 1px solid #f0f3f6 */
}

.sidebarlinks {

    border-bottom: 1px solid #f0f3f6;

}

.sidebarlinks a {
    text-decoration: none;
    color: #152934;
    width: 100%;
    height: 100%;
    display: block;
    padding: 10px 0px 10px 10px;
}

.sidebarlinks:hover {
    background: #f0f3f6;
    cursor: pointer;
    /* border-right: 4px solid #152934; */
}

.bluehightlight {
    padding: 10px;
    background: rgb(190, 213, 250);
    margin: 0px 0px 0px 0px;
}



.active {
    border-left: 4px solid #152934;
    background: #f0f3f6;
}

.pad20 {
    padding: 20px;
}

.p10 {
    padding: 10px;
}

.displayinline {
    display: inline;
}

.disableoverlay {
    position: fixed;
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    background: rgba(255, 255, 255, 0.5);
    z-index: 1000;
}

.clickable {
    position: relative;
    display: inline-block;
}

.mt10 {
    margin-top: 10px;
}

.mb10 {
    margin-bottom: 10px;
}

.sidebarcontent {
    -webkit-flex: 1 1;
            flex: 1 1;
    padding: 20px;
}

.pagetitle {
    border-bottom: 2px solid #f0f3f6;
    padding: 10px 10px 10px 0px;
    font-size: 1.4em;
}

.maincontent {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

}

.hidden {
    display: none;
}

.bottomcontent {
    -webkit-flex: 2 1;
            flex: 2 1;
    background: white
}

.containerwrap>* {}

@media all and (max-width: 540px) {
    .aside {
        -webkit-flex: 1 1 auto;
                flex: 1 1 auto;
    }
}

@media all and (min-width: 800px) {
    .maincontent {
        -webkit-flex: 3 1;
                flex: 3 1;
    }

    .sidebar {
        -webkit-order: 1;
                order: 1;
    }

    .maincontent {
        -webkit-order: 2;
                order: 2;
    }

    .footer {
        -webkit-order: 3;
                order: 3;
    }
}

.examplebox {
    padding-bottom: 100px;
    margin-bottom: 50px;
    background: #fff;
}

.lighttext {
    font-weight: 200
}

.authornames {
    padding: 10px 0px 0px 0px;
    font-weight: bold;
}

.sidebartitle {
    margin: 0px 0px 10px 0px
}

.sidebardesc {
    padding: 10px;
    margin-top: 15px;
    background: #f0f3f6;
}

.maintitle {
    margin: 0px 0px 30px 0px
}



#footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    border-top: 1px solid #f0f3f6;
    padding: 14px;
    background: #fff;
}

#footer a {
    text-decoration: none;
}
.handimagebox {
    width: 100px;
    height: 100px;
    border-radius: 2px;
    margin: 0px 10px 5px 0px;
    display: inline-block;
    z-index: 0;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    border-bottom: 6px solid #fff;
}

.handimageselected {
    border-bottom: 6px solid #0063FF;
}

.flipimagecheckbox {
    float: right;
}

.handimagebox:hover {
    cursor: pointer;
    border-bottom: 6px solid #0063FF;
    /* transform: scale(1.4); */
    z-index: 1000;
}

.videoboxes {
    z-index: -100;
    min-width: 920px;
}

.videobox {
    display: none;
    border-radius: 2px;
    margin: 0px 6px 0px 0px;
    object-fit: cover;
}

.canvasbox {
    border-radius: 3px;
    float: right;
    margin-right: 10px;
    width: 450px;
    height: 380px;
    border-bottom: 3px solid #0063FF;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2), 0 4px 10px 0 rgba(0, 0, 0, 0.19);
}

.slidecontainer {
    width: 80%;
    /* Width of the outside container */
    margin-bottom: 15px;
}

.confidencethreshold {
    margin: 0px 0px 0px 10px;
    font-weight: bold;
}

.loadingtitle {
    position: absolute;
    padding: 8px 0px 0px 5px;
    font-weight: bold;
}

/* The slider itself */
.slider {
    -webkit-appearance: none;
    /* Override default CSS styles */
    -moz-appearance: none;
         appearance: none;
    width: 100%;
    /* Full-width */
    height: 8px;
    /* Specified height */
    background: #d3d3d3;
    /* Grey background */
    outline: none;
    /* Remove outline */
    opacity: 0.7;
    /* Set transparency (for mouse-over effects on hover) */
    -webkit-transition: .2s;
    /* 0.2 seconds transition on hover */
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
}

/* Mouse-over effects */
.slider:hover {
    opacity: 1;
    /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    /* Override default look */
    appearance: none;
    width: 20px;
    /* Set a specific slider handle width */
    height: 25px;
    /* Slider handle height */
    background: #0063FF;
    border-radius: 4px;
    /* Green background */
    cursor: pointer;
    /* Cursor on hover */
}

.slider::-moz-range-thumb {
    width: 25px;
    /* Set a specific slider handle width */
    height: 25px;
    /* Slider handle height */
    background: #0063FF;
    /* Green background */
    cursor: pointer;
    /* Cursor on hover */
}

/* Pattern styles */
.containerflex {
    display: -webkit-flex;
    display: flex;
}

.left-half {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.right-half {
    width: 450;
}
